@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Arapey";
  src: url(../fonts/Arapey/Arapey-Regular.ttf);
  font-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: 'Montserrat';
  background: #FFFFFF;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after, ::before {
  font-family: 'fontawesome';
}

.icon-burger::before {
  content: "\f0c9";
  font-size: 30px;
  color: #1D1D1D;
}

.icon-close::before {
  content: "\f00d";
  color: #1D1D1D;
  font-size: 50px;
}

.nav-block {
  position: sticky;
  top: 0;
  z-index: 999;
  background: #FFFFFF;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 576px) {
  .nav {
    flex-direction: row;
    padding: 10px;
  }
}

.nav__burger {
  background: none;
  outline: none;
  border: none;
  display: none;
}

@media (max-width: 576px) {
  .nav__burger {
    display: block;
    position: absolute;
    z-index: 999;
    right: 10px;
  }
}

@media (max-width: 768px) {
  .nav {
    flex-direction: column;
  }
}

.logo__img {
  height: 60px;
}

@media (max-width: 576px) {
  .logo__img {
    height: 40px;
  }
}

.menu {
  display: flex;
}

@media (max-width: 576px) {
  .menu {
    display: none;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    background: #FFFFFF;
    height: 100%;
    padding-top: 25%;
    flex-direction: column;
    text-align: center;
  }
}

.menu__link {
  padding: 25px 15px;
  color: #1D1D1D;
  transition: 0.5s;
  display: block;
}

.menu__link:hover {
  background: #F1F1F1;
}

@media (max-width: 768px) {
  .menu__link {
    padding: 10px;
  }
}

@media (max-width: 576px) {
  .menu__link {
    display: inline-block;
  }
  .menu__link:hover {
    background: none;
  }
}

.burger::before {
  content: "\f0c9";
}

.cancel::before {
  content: "\f00d";
}

.header-heght {
  height: 100vh;
}

.banner {
  width: 100%;
  height: 50vh;
  position: relative;
}

.dark__fone {
  height: 100%;
  background: rgba(0, 0, 0, 0.322);
  position: relative;
}

.slide__img {
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.banner__block {
  background: #131313;
}

.banner__content {
  text-align: center;
  max-width: 700px;
  margin: auto;
  color: #FFFFFF;
  padding: 50px 0 100px 0;
}

.banner__info {
  font-size: 19px;
  margin-top: 10px;
  margin-bottom: 30px;
}

.banner__title {
  font-size: 50px;
  text-transform: uppercase;
  font-family: 'Montserrat';
}

.banner__link {
  color: #FFFFFF;
  margin-top: 20px;
  display: inline-block;
}

.skills {
  padding: 60px 0 80px 0;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 576px) {
  .skills {
    justify-content: center;
  }
}

.skills__block {
  width: 16.66667%;
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
}

@media (max-width: 992px) {
  .skills__block {
    width: 41.66667%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
  }
}

@media (max-width: 576px) {
  .skills__block {
    width: 83.33333%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.skills__img {
  width: 150px;
  border-radius: 50%;
}

.skills__title {
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 15px;
}

.catalog {
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 992px) {
  .catalog {
    justify-content: center;
  }
}

.catalog__title {
  text-align: center;
  font-size: 40px;
  font-family: 'Montserrat';
  font-weight: 400;
  margin-top: 50px;
}

.catalog__info {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 40px;
}

.catalog__more {
  color: #1D1D1D;
  background: #FEFC8F;
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-top: 40px;
  margin-bottom: 60px;
  transition: 0.5s;
}

.catalog__more:hover {
  background: #131313;
  color: #FFFFFF;
}

.item {
  color: #FFFFFF;
  width: 33.33333%;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 768px) {
  .item {
    width: 83.33333%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 992px) {
  .item {
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
    margin-top: 20px;
  }
}

.item__title {
  text-transform: uppercase;
  font-size: 18px;
  margin-bottom: 40px;
}

.item__info {
  font-size: 13px;
}

.item__block {
  background: #131313;
  margin-top: -4px;
  padding: 40px 10px;
  text-align: center;
}

.item__img {
  width: 100%;
}

.clients {
  margin-top: 30px;
  margin-bottom: 50px;
}

.clients__title {
  text-align: center;
  font-size: 40px;
  color: #131313;
  margin-bottom: 60px;
  font-weight: 400;
}

.client {
  width: 16.66667%;
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
  margin-bottom: 30px;
}

@media (max-width: 992px) {
  .client {
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 40px;
  }
}

.client__img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

@media (max-width: 576px) {
  .client__img {
    width: 150px;
  }
}

.client__title {
  font-weight: 400;
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 10px;
}

@media (max-width: 576px) {
  .client__title {
    font-size: 23px;
  }
}

.client__client__info {
  font-size: 13px;
}

.footer {
  background: #131313;
  color: #FFFFFF;
  padding: 40px 0;
  margin-top: 100px;
}

.footer .container {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 992px) {
  .footer .container {
    flex-direction: column;
    justify-content: center;
  }
}

.footer__title {
  font-weight: 400;
  font-size: 35px;
  margin-bottom: 30px;
}

.footer__addres {
  margin-bottom: 10px;
}

.footer a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 10px;
}

.footer a:hover {
  color: #ff3d3d;
}

@media (max-width: 992px) {
  .map {
    width: 200px;
  }
}

.about__main {
  display: flex;
  background: #ededed;
  align-items: center;
}

@media (max-width: 992px) {
  .about__main {
    flex-direction: column;
  }
}

.about__img {
  width: 50%;
  height: 70vh;
  object-fit: cover;
}

@media (max-width: 992px) {
  .about__img {
    width: 100%;
    height: 40vh;
  }
}

.about__info {
  max-width: 500px;
  margin-left: 120px;
  margin-right: 40px;
}

@media (max-width: 992px) {
  .about__info {
    margin: 30px 10px 30px 10px;
    text-align: center;
  }
}

.about__title {
  text-transform: uppercase;
  margin-bottom: 30px;
}

.about__history {
  margin-bottom: 20px;
}

.products__title {
  font-size: 40px;
  text-transform: uppercase;
  font-weight: 400;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 40px;
}

.product {
  width: 33.33333%;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 40px;
}

@media (max-width: 992px) {
  .product {
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 576px) {
  .product {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.product__img {
  width: 100%;
  object-fit: cover;
}

.product__block {
  background: #131313;
  margin-top: -4px;
  color: #FFFFFF;
  padding: 40px 10px;
  text-align: center;
}

.product__title {
  font-size: 18px;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.product__info {
  font-size: 13px;
}
